<template>
  <div class="time_root">
    <div class="left">
      <div v-for="(item, i) in lists" :class="{active: item.id === current }" :key="i" @click="current = item.id">
        <span>{{item.year}}</span>
        {{item.id}}
      </div>
    </div>
    <div class="right">
      {{detail}}
    </div>
  </div>
</template>

<script>
  import * as R from 'ramda'
  import Fetch from "../util/Fetch";
  import http from "../util/http";
  import moment from 'moment'
  export default {
    components: {},
    async mounted() {
      let res = await Fetch(http.ParkHistory);
      res.data.map((v,i) => {
          v.id = i+1;
          v.year = moment(v.Date).year();
      })
      this.lists = res.data;
      this.current = 1;
    },
    data() {
      return {
        lists: [{}],
        current: 0,
      }
    },
    computed: {
      detail: function ( ) {
        return this.current ?  R.find(R.propEq("id", this.current))(this.lists).Cotent || "" : ""
      }
    },
    methods: {
    }
  }
</script>

